					line-height: 2.3em;
					color: #FFFFFF;
					
					&:hover {
						text-decoration: none;
					}
				}
			}
			.checkbox { 
				.make-sm-column(10); text-align: left;  padding-top: 28px; 
				input { margin-top: 3px; }
				color: rgb(158,158,158);

				& h4 {
					line-height: 0em;
					font-size: 1.786em;
