/* leadtracker ****************/

@new: #6be757;
@qualify:  #93d0db;
@nurture: #a1a915;
@watch: #775ff0;
@hot: #ba0000;
@cold: #0030ff;
@closed: #ff00f0;
@pending: #e4800b;
@trash: #905e08;
@all: #585858;

body.leadtracker {  
	
	background-color: #fafaf6;
	overflow-x: hidden;

	// icons and colors
	& .new { color: @new; }
	& .qualify { color: @qualify; }
	& .nurture { color: @nurture; }
	& .watch { color: @watch; }
	& .hot { color: @hot; }

	& .cold { color: @cold; }
	& .closed { color: @closed; }
	& .pending { color: @pending; }
	& .trash { color: @trash; }
	& .all { color: @all; }


	& .calls > i {
		background: url('../img/leadtracker-icons-calls.png') no-repeat 0 0;
		height: 25px;
		width: 25px;
		overflow: hidden;
		display: inline-block;
	}
	& .calls.talking > i { background-position: 0 0; }
	& .calls.valid > i { background-position: 0 -29px; }
	& .calls.unknown > i { background-position: 0 -60px; }
	& .calls.do-not-call > i { background-position: 0 -84px; }
	& .calls.wrong > i { background-position: 0 -117px; }

	& .emails > i {
		background: url('../img/leadtracker-icons-emails.png') no-repeat 0 0;
		height: 25px;
		width: 30px;
		overflow: hidden;
		display: inline-block;
	}
	& .emails.one-way > i { background-position: 0 0; }
	& .emails.valid > i { background-position: 0 -25px; }
	& .emails.unknown > i { background-position: 0 -54px; }
	& .emails.opt-out > i { background-position: 0 -82px; }
	& .emails.invalid > i { background-position: 0 -108px; }
	& .emails.two-way > i { background-position: 0 -136px; }
	

	// filter row on top
	& > .container > .filters {

		.make-row();
		margin-top:30px;

		& > button {
			border-top-right-radius: 9px; 
			border-top-left-radius: 9px;
			border-color: transparent;
			height: 40px;
			vertical-align: middle;
			background: #ffffff;
			.text-uppercase;
			width:8.5%;
			margin: 0 .5%;
			.drop-shadow(0, 0, 1px, 0.2);
		}
	}

	& > .container > section {
		.make-row();

		& > .panel {
			background: #fafafa;
			margin-top: -3px;
			.make-sm-column(12);

			& > .panel-heading {

				font-size: 14px;
				padding: 30px;
				& > ul > li {
					vertical-align: middle;
					display: inline-block;
					margin-right: 4.5%;

					& h2 { font-size: 28px; .text-shadow(); }
					& > input { width:20px; height: 20px; padding: 2px 4px; display: inline-block; }
				}
			}

			& > .panel-body {
				margin:0;
				padding: 0;
				color: #9c9c9c;
				& > table {
					
					border-bottom: 1px solid #e0e0df;

					& > thead {
					.p_gradient();
					.make-row();
					
						& > tr > th {
							padding: 20px 8px;
							font-size: 14px;
							font-weight: normal;
							text-align: center;
							border-bottom: none;
							color: #585858;
							.text-shadow(#fff); 
						}

						& > tr > th:first-of-type {
							padding-left: 18px;
						}
					}
					& > tbody > tr:nth-child(odd) { 
						background: #f7f7f7;
						border-bottom: 1px solid #dadada;
						border-top: 1px solid #fff;
					}
					& > tbody > tr:nth-child(even) { 
						background: #ebeef0;
					}

					& > tbody > tr > td {
						
						text-align: center;
						font-size: 14px;
						vertical-align: middle;
						// width: 6.25%;
						border-right: 1px solid #dadada;
						

						& .bootstrap-select .btn { line-height: 15px; font-size: 12px; }
						&.contact,&.date,&.contact, &.last-visit, &.created, &.action, &.price { font-size: 12px; }
						&.status {  
							.text-uppercase();
						}

						&.calls, &.emails, &.drip {
							line-height: 0;
							padding: 0 8px;
							width: 60px;
						}
						
						&.action .bootstrap-select { width: 120px; }
						

						&.drip > i { 
							height: 24px;
							width: 24px;
							overflow: hidden;
							display: inline-block;
							border:4px solid #fff;
							.drop-shadow(0, 0, 1px, 0.1);
						}

						&.drip.ok > i { 
							background-color: @new;  
						}

						&.agent { line-height: 15px; }
						
					}

				}
				& nav { text-align: center; }
			}
		}

		


		&#key {

			margin-top: 50px;
			line-height: 40px;
			
			& > ul { width: 20%; float: left; }
			& > ul:first-child { width: 35% }

		}
	}
		
	& #slide-panel {
		position: absolute;
		top: 0;
		right: 0;
		width:300px;
		height:800px;
		padding:0px;
		background:#eee;
		margin-right:-300px;
		float: right;
		max-width: 300px;
		.drop-shadow( 0px, 0px, 1px, 0.2);

		& > div {
			min-height: 200px;
			border-top: 1px solid #dadada;
			padding: 0px 20px 10px;
			
			& > h3 { font-size: 14px; font-weight: bold; text-align: center;}
		}
	}
	& #opener {

	   float:left;
	   margin: 76px 0px 0px -96px;
	   width: 160px;
	   border-top-right-radius: 9px;
	   border-top-left-radius: 9px;
	   // background-color: @hot;
	   .btn-primary();
	   color: #ffffff;
		-webkit-transform: rotate(270deg);
		-moz-transform: rotate(270deg);
		-o-transform: rotate(270deg);
		writing-mode: lr-tb;

		& i { margin: 0 8px 0 0; }
	}
	& #content
	{
	   height: 500px;
	   width: 100%;
	   float: right;
		padding-left: 5%;
	}
}


/* contact-detail ****************/

.contact-detail { 

	& form {
		.form-horizontal();
	}
	& ul { 
		.clearfix();

		& li.save, li.delete, li.copy { float: left; width: auto !important;  padding-left: 2px; padding-right: 2px; } 
		& li.save a, li.delete a, li.copy a  { .btn(); .btn-sm(); .rounded(15px);  width: 100%;  }
		& li.save a { .btn-primary; }
		& li.delete a { .btn-danger; }
		& li.copy a { .btn-info; }
	}

	& .btn-default { .p_gradient(); color: #555; }

}


.contact-detail section:first-of-type { 
	.make-row();
	padding-left: 15px; 
	padding-right: 15px;
	margin-top: 20px; 
	
	& > .panel {

		.make-sm-column(12);  
	
		& > .panel-body {
			margin:0;
			padding: 0 15px;

			& > header {
				.make-row();
				.p_gradient();
				padding: 15px 30px;
				vertical-align: middle;
				& > * { display: inline-block; }
				& > span { margin: 0 30px 0 0; font-size: 28px; }
				& > label { width: auto; margin-right: 10px; }
				& > input { width: 20%; height: 32px; }
			}
			
			& > .forms {
				
				.make-row();
				background-color: #f7f7f7;
				
				
				
				& > ul:nth-child(even) {
					background-color: #edeff0;
					border-left: 1px solid #cccccc;
					border-right: 1px solid #cccccc;
					padding-bottom: 30px;

				}

				& > ul {  
					.make-sm-column(4);
					.clearfix();
					padding: 20px 0;

					& > li {
						margin-bottom: 2px;
						clear: both;
						padding-left: 15px;

						& > label {
							font-size: 13px;
							float:left;
							width: 30%;
							padding-top: 6px;
						}
						& > input, & > .bootstrap-select {
							font-size: 13px;
							
							
							width: 62%;
							
						}

						& > input {
							padding: 3px 12px;
							.p_gradient();
							height: 32px;
						}

						&.drip{  
							margin: 10px 0;
							& label {  
								width: 33%;
							   padding-right: 0;
							   margin-right: 0;
							}
							& li.save, li.copy, li.delete { width: 20% !important; }
						}
						&.non-labeled { padding-left: 15px; margin: 20px 0; }
						& > .smallprint { 
							text-align: center;  
							font-size: 11px;
							font-style: italic;
						}
					}

					.state { 
						width: 55%; 
						display: inline-block;
						& label { width: 57%; }
						& .bootstrap-select { display: inline-block; width:38%; } 
					}
					.zip {
						width: 30%;
						display: inline-block;
						& label { width: 30%; }
						& input { display: inline-block; width:64%; }
					}
					.country {
						.clearfix();
						margin-bottom: 2px;
						& > .bootstrap-select { float: left; width: 62%; }


					}
					
				}

				& ul.doubled {
					
					& > li {  }
					& > li > label { width: 55%; padding-right: 2px; padding-left: 5px; }
					& > li > input { padding: 3px; width: 45%; }

					& > li:nth-child(odd) { 
						width: 50%; 
						display: inline-block; 
						padding-left: 5px;
						// & > label { width: 69%; }


					}
					& > li:nth-child(even) { 
						width: 48%; 
						margin-left: 0px; 
						display: inline-block;
						padding-left: 5px;
						padding-right: 5px;
					}
					& > li.full { 
						width: 100%;
						padding-left: 5px;
						padding-right: 0;  
						& > label { width: 40%; padding-right: 2px; padding-left: 5px; }
						& > input { width: 54%; }

					}
					& .purchases { 
						margin-bottom: 0; padding-bottom: 0;
						& > label { margin-bottom: 0; }
					}
					& .reminder { margin-bottom: 10px; 

					}
					& .contact-type { 
						width: 96% !important;
						margin-left: 4px; 
						
					}
					& .date { 
						float: left;
						width: 46% !important;
						margin-right: 12px;
						margin-top: 4px;
						padding: 4px 12px;
						margin-left: 4px;
					}
					& .time { 
						width: 46% !important;
						margin-top: 4px;
						& > button { .p_gradient(); }
					}

			 		& .notes {
			 			& > label { width: 100% !important; }
			 			& > textarea { width: 95% !important; margin-left: 5px; .p_gradient(); .bordered(#ccc,#ccc,#ccc,#ccc); resize: none; }
			 			& li.save, li.copy, li.delete {padding-left: 0; padding-right: 0; margin: 5px 2px;}
			 			& a { font-size: 11px;  }
			 		}
			 	}
			}	

		}
	}
}
.contact-detail section#subpanel {
	.make-row();
	padding-left: 15px; 
	padding-right: 15px;
	margin-top: 0;
	& > .panel {

		.make-sm-column(12);  

		& > .panel-body {
			margin:0;
			padding: 0 15px;
			
			& > nav { 
				.make-row();
				.clearfix();	
				& > div {
					margin: 2em 0% 0;
					width: auto;
					float: left;
					background: none;

					& > .btn { background: none; color: #444; }
					& > .btn:hover, .btn:focus {
					    color: #333;
					    text-decoration: underline;
					}
					& .btn:active, .btn.active { 
						color: #333;
						outline: 0px none;
						box-shadow: none;
						border-top: 1px solid #CCC;
						border-right: 1px solid #CCC;
						border-left: 1px solid #CCC;
						background: #f8f8f8 !important;
						z-index: 100000;
						top: 2px;
						position: relative;
					}
				}
			}
			& > #content-area { 

				& > table {
					
					.bordered( #ccc, #e0e0df, #e0e0df, #e0e0df);
					& > thead {
					.p_gradient();
					.make-row();
					
						& > tr > th {
							padding: 20px 8px;
							font-size: 14px;
							font-weight: normal;
							text-align: center;
							border-bottom: none;
							color: #585858;
							.text-shadow(#fff); 
						}

						& > tr > th:first-of-type {
							padding-left: 18px;
						}
					}
					& > tbody > tr:nth-child(odd) { 
						background: #f7f7f7;
						border-bottom: 1px solid #dadada;
						border-top: 1px solid #fff;
					}
					& > tbody > tr:nth-child(even) { 
						background: #ebeef0;
					}

					& > tbody > tr > td {
						
						text-align: center;
						font-size: 14px;
						vertical-align: middle;
						// width: 6.25%;
						border-right: 1px solid #dadada;
						

						& .bootstrap-select .btn { line-height: 15px; font-size: 12px; }
						&.contact,&.date,&.contact, &.last-visit, &.created, &.action, &.price { font-size: 12px; }
						&.status {  
							.text-uppercase();
						}

						&.calls, &.emails, &.drip {
							line-height: 0;
							padding: 0 8px;
							width: 60px;
						}
						
						&.action .bootstrap-select { width: 120px; }
						

						&.drip > i { 
							height: 24px;
							width: 24px;
							overflow: hidden;
							display: inline-block;
							border:4px solid #fff;
							.drop-shadow(0, 0, 1px, 0.1);
						}

						&.drip.ok > i { 
							background-color: @new;  
						}

						&.agent { line-height: 15px; }
						
					}

				}










				& th:first-child { padding-left: 20px; }
				& th { padding: 2em 0 1em; }
				& th:last-child { width: 150px; }
			}
		}
	}

}
			
	
